.react-slideshow-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center
}

.react-slideshow-container .nav {
    z-index: 10
}

.react-slideshow-container .default-nav {
    height: 30px;
    background: rgba(255, 255, 255, 0.6);
    width: 30px;
    border: 0;
    text-align: center;
    cursor: pointer;
    color: #fff;
    border-radius: 50%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center
}

.react-slideshow-container .default-nav:hover, .react-slideshow-container .default-nav:focus {
    background: #fff;
    color: #666;
    outline: 0
}

.react-slideshow-container .default-nav.disabled:hover {
    cursor: not-allowed
}


.react-slideshow-container .default-nav:first-of-type {
    left: 20px;
    border-right: 0;
    border-top: 0;
    position: absolute;
    opacity: 0;
}

.react-slideshow-container .default-nav:last-of-type {
    position: absolute;
    right: 20px;
    opacity: 0;
}
.react-slideshow-container .default-nav:hover{
    opacity: 0.5;
}

.react-slideshow-container+div.indicators {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    margin-top: 20px
}

.react-slideshow-container+div.indicators li {
    display: inline-block;
    position: relative;
    width: 7px;
    height: 7px;
    padding: 5px;
    margin: 0
}

.react-slideshow-container+div.indicators .each-slideshow-indicator {
    border: 0;
    opacity: .25;
    cursor: pointer;
    background: transparent;
    color: transparent
}
.each-slide img{
    width: 100%;
    height: 100%;
}

.react-slideshow-container+div.indicators .each-slideshow-indicator:before {
    position: absolute;
    top: 0;
    left: 0;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    content: '';
    background: #000;
    text-align: center
}

.react-slideshow-container+div.indicators .each-slideshow-indicator:hover, .react-slideshow-container+div.indicators .each-slideshow-indicator.active, .react-slideshow-container+div.indicators .each-slideshow-indicator:focus {
    opacity: .75;
    outline: 0
}

.react-slideshow-fade-wrapper {
    width: 100%;
    overflow: hidden
}

.react-slideshow-fade-wrapper .react-slideshow-fade-images-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap
}

.react-slideshow-fade-wrapper .react-slideshow-fade-images-wrap>div {
    position: relative;
    opacity: 0
}

.react-slideshow-wrapper.slide {
    width: 100%;
    overflow: hidden
}

.react-slideshow-wrapper .images-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap
}

.react-slideshow-zoom-wrapper {
    width: 100%;
    overflow: hidden
}

.react-slideshow-zoom-wrapper .zoom-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    overflow: hidden
}

.react-slideshow-zoom-wrapper .zoom-wrapper>div {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex
}